import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Row, Label } from "reactstrap";
import _ from "lodash";
import Loader from "../../../components/Loader/Loader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import InputElement from "../../../components/InputElement/InputElement";
import AppConfig from "../../../constants/AppConfig";
import CollapseCards from "../../../components/Cards/CollapseCards";
import AudioAllocationGrid from "../AudioAllocationGrid";
import AudioPlayer from "../../../components/AudioPlayer/AudioPlayer";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import { Link } from "react-router-dom";
import ImageAllocationGrid from "../ImageAllocationGrid";

class LiveStockMaterialForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      liveStockMaterial: {
        Id: "",
        LiveStockId: "",
        LiveStock_StepId: "",
        LiveMaterialName: "",
        LiveMaterialDesc: "",
        Category: "",
        ImageURL: "",
        Cost: "",
        Quantity: "",
        Quantity_measured: "",
        Cost_measured: "",
        CreatedOn: "",
        CreatedBy: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true,
        renderURL: "",
        LiveStockIdRequired: "",
        LiveStock_StepIdRequired: "",
        LiveMaterialNameRequired: "",
        CategoryRequired: ""
      },
      itemToDelete: "",
      modalStatus: false,
      stepOptions: [],
      stepDisabled: true,
      audioGridOpen: false,
      activeAudioAllocation: [],
      imageGridOpen: false,
      imageAllocation: [],
      audioToDelete: {}
    };
  }

  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      if (this.props.activeLiveStockMaterial.length !== 0) {
        let id = this.props.match.params.id;
        this.setCurrentMaterialToState(id);
      }
      // setTimeout(() => {
      //   this.setState({
      //     loading: false,
      //     updateFlag: true
      //   });
      // }, 2000);
    } else {
      setTimeout(() => {
        this.setState({
          loading: false
        });
      }, 1000);
    }
    if (this.props.liveStockMaterialError) {
      Toaster.Toaster(
        "Something went wrong !",
        this.props.liveStockMaterialError
      );
    }
  }
  setCurrentMaterialToState(id) {
    this.props.getLivestockMaterialAudioAllocations(id);
    let liveStockMaterial = _.find(this.props.activeLiveStockMaterial, function(
      material
    ) {
      return material.Id === parseInt(id);
    });
    if (liveStockMaterial !== undefined) {
      if (liveStockMaterial.ImageURL) {
        liveStockMaterial.renderURL = `${AppConfig.serverURL}/${liveStockMaterial.ImageURL}`;
      }
      let stepOptions = _.filter(this.props.liveStockStepsList, {
        LiveStockId: liveStockMaterial.LiveStockId
      });
      let imageAllocation = _.filter(this.props.imageFiles, {
        FilePath: liveStockMaterial.renderURL
      });
      setTimeout(() => {
        let activeAudioAllocation = _.filter(this.props.currentMaterialAudios, {
          Active: true
        });
        this.setState({
          updateFlag: true,
          liveStockMaterial: liveStockMaterial,
          loading: false,
          stepOptions: stepOptions,
          stepDisabled: false,
          activeAudioAllocation: activeAudioAllocation,
          audioGridOpen: true,
          imageGridOpen: true,
          imageAllocation: imageAllocation
        });
      }, 3000);
    } else {
      this.props.history.push("/liveStock/liveStockMaterials");
    }
  }
  onChangeName(event) {
    let liveStockMaterial = { ...this.state.liveStockMaterial };
    liveStockMaterial[event.target.name] = event.target.value;
    liveStockMaterial[event.target.name + "Required"] = false;
    this.setState({ liveStockMaterial });
  }
  onChangeLiveStock(value) {
    let liveStockMaterial = { ...this.state.liveStockMaterial };
    if (value) {
      liveStockMaterial.LiveStockId = value;
      liveStockMaterial.LiveStockIdRequired = false;
      liveStockMaterial.LiveStock_StepId = "";
      let stepOptions = _.filter(this.props.liveStockStepsList, {
        LiveStockId: value
      });
      this.setState({ liveStockMaterial, stepOptions, stepDisabled: false });
    } else {
      liveStockMaterial.LiveStockId = value;
      liveStockMaterial.LiveStock_StepId = "";
      let stepOptions = _.filter(this.props.liveStockStepsList, {
        LiveStockId: value
      });
      this.setState({ liveStockMaterial, stepOptions: [], stepDisabled: true });
    }
  }
  onChangeLiveStockStep(value) {
    let liveStockMaterial = { ...this.state.liveStockMaterial };
    liveStockMaterial.LiveStock_StepId = value;
    liveStockMaterial.LiveStock_StepIdRequired = false;
    this.setState({ liveStockMaterial });
  }
  onChangeCategory(value) {
    let liveStockMaterial = { ...this.state.liveStockMaterial };
    liveStockMaterial.Category = value;
    liveStockMaterial.CategoryRequired = false;
    this.setState({ liveStockMaterial });
  }
  onSubmit() {
    let liveStockMaterial = { ...this.state.liveStockMaterial };
    if (this.validData()) {
      liveStockMaterial.LiveMaterialName = liveStockMaterial.LiveMaterialName.trim();
      liveStockMaterial.LiveMaterialDesc = liveStockMaterial.LiveMaterialDesc.trim();
      liveStockMaterial.Cost_measured = liveStockMaterial.Cost_measured.trim();
      liveStockMaterial.Quantity_measured = liveStockMaterial.Quantity_measured.trim();

      if (this.state.updateFlag) {
        let materialUpdate = _.pick(liveStockMaterial, [
          "Id",
          "LiveStockId",
          "LiveStock_StepId",
          "LiveMaterialName",
          "LiveMaterialDesc",
          "Category",
          "ImageURL",
          "Cost",
          "Quantity",
          "Quantity_measured",
          "Cost_measured",
          "UpdatedBy",
          "UpdatedOn",
          "Active"
        ]);
        materialUpdate.UpdatedBy = localStorage.getItem("user");
        materialUpdate.UpdatedOn = new Date();
        materialUpdate.Active = true;
        this.props.updateLiveStockMaterial(materialUpdate.Id, materialUpdate);
      } else {
        let materialCreate = _.pick(liveStockMaterial, [
          "LiveStockId",
          "LiveStock_StepId",
          "LiveMaterialName",
          "LiveMaterialDesc",
          "Category",
          "Cost",
          "Quantity",
          "Quantity_measured",
          "Cost_measured",
          "CreatedOn",
          "CreatedBy",
          "Active"
        ]);
        materialCreate.CreatedBy = localStorage.getItem("user");
        materialCreate.CreatedOn = new Date();
        materialCreate.Active = true;
        this.props.createLiveStockMaterial(materialCreate);
      }
      this.setState({ loading: true });
      let message = "";
      let compRef = this;
      setTimeout(() => {
        compRef.props.liveStockMaterialError
          ? (message = compRef.props.liveStockMaterialError)
          : compRef.state.updateFlag
            ? (message = "Livestock step material updated successfully")
            : (message = "Livestock step material created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.liveStockMaterialError);
        setTimeout(() => {
          if (!compRef.props.liveStockMaterialError) {
            compRef.onReset();
            compRef.props.history.push("/liveStock/liveStockMaterials");
          }
        }, 1000);
      }, 1000);
    }
  }
  validData() {
    let liveStockMaterial = { ...this.state.liveStockMaterial };
    if (
      liveStockMaterial.LiveStockId &&
      liveStockMaterial.LiveStock_StepId &&
      liveStockMaterial.LiveMaterialName.trim().length > 0 &&
      liveStockMaterial.Category
    ) {
      return true;
    } else {
      if (!liveStockMaterial.LiveStockId)
        liveStockMaterial.LiveStockIdRequired = true;
      if (!liveStockMaterial.LiveStock_StepId)
        liveStockMaterial.LiveStock_StepIdRequired = true;
      if (
        !liveStockMaterial.LiveMaterialName ||
        liveStockMaterial.LiveMaterialName.trim().length <= 0
      )
        liveStockMaterial.LiveMaterialNameRequired = true;
      if (!liveStockMaterial.Category)
        liveStockMaterial.CategoryRequired = true;
      this.setState({ liveStockMaterial });
      return false;
    }
  }
  onReset() {
    this.setState({
      liveStockMaterial: {
        Id: "",
        LiveStockId: "",
        LiveStock_StepId: "",
        LiveMaterialName: "",
        LiveMaterialDesc: "",
        Category: "",
        ImageURL: "",
        Cost: "",
        Quantity: "",
        Quantity_measured: "",
        Cost_measured: "",
        CreatedOn: "",
        CreatedBy: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true,
        renderURL: "",
        LiveStockIdRequired: "",
        LiveStock_StepIdRequired: "",
        LiveMaterialNameRequired: "",
        CategoryRequired: ""
      },
      stepOptions: [],
      stepDisabled: true
    });
  }
  setInputToNumeric(e) {
    const re = /[0-9]+/g;
    if (!re.test(e.key)) {
      e.preventDefault();
    }
  }
  /**--------------AUDIO ALLOCATION FUNCTIONS----------------- */
  onModalToggle(itemToDelete) {
    this.setState({
      itemToDelete: itemToDelete,
      modalStatus: !this.state.modalStatus
    });
  }
  audioToggleCollapse() {
    this.setState({
      audioGridOpen: !this.state.audioGridOpen
    });
  }
  onAddAudio() {
    if (this.props.match.params.id !== undefined) {
      this.props.history.push(
        `/liveStock/audioAllocation/${"liveStockMaterial"}/${this.props.match
          .params.id}`
      );
    }
  }
  playAudio(cell, row) {
    return <AudioPlayer autoPlay={false} source={row.FilePath} />;
  }
  onDeleteAudio(cell, row) {
    return (
      <Link to={this} onClick={() => this.deleteAudio(row)}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }
  deleteAudio(row) {
    this.setState({ audioToDelete: row });
    this.onModalToggle("audio");
  }
  onConfirmDeleteAudio() {
    let audioToDelete = { ...this.state.audioToDelete };
    let compRef = this;
    audioToDelete.Active = false;
    audioToDelete.ActiveBy = localStorage.getItem("user");
    audioToDelete.ActiveOn = new Date();
    this.props.deactivateLivestockMaterialAudioAllocations(audioToDelete.Id, audioToDelete);
    let displayMessage = "Livestock material audio removed successfully";
    setTimeout(() => {
      let message = "";
      compRef.props.liveStockMaterialError
        ? (message = "Something went wrong !")
        : (message = displayMessage);
      Toaster.Toaster(message, compRef.props.liveStockMaterialError);
      compRef.setCurrentMaterialToState(this.props.match.params.id);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  /**------------------------Image Allocation functions ---------------------*/
  imageToggleCollapse() {
    this.setState({
      imageGridOpen: !this.state.imageGridOpen
    });
  }
  onAddImage() {
    if (this.props.match.params.id !== undefined) {
      this.props.history.push(
        `/liveStock/imageAllocation/${"liveStockMaterial"}/${this.props.match
          .params.id}`
      );
    }
  }
  showImage(cell, row) {
    if (row.FilePath) {
      return (
        <img src={row.FilePath} style={{ height: 50, width: 50 }} alt="" />
      );
    } else {
      return;
    }
  }
  onDeleteImage(cell, row) {
    return (
      <Link to={this} onClick={() => this.onModalToggle("image")}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }
  onConfirmDeleteImage() {
    let liveStockMaterial = { ...this.state.liveStockMaterial };
    //this.setState({ notificationItem: "image" });
    let compRef = this;
    liveStockMaterial.UpdatedBy = localStorage.getItem("user");
    liveStockMaterial.UpdatedOn = new Date();
    liveStockMaterial.ImageURL = "";
    let image = _.pick(liveStockMaterial, [
      "Id",
      "ImageURL",
      "UpdatedBy",
      "UpdatedOn"
    ]);
    this.props.updateMaterialImage(image);
    let displayMessage = "Livestock step material image removed successfully";
    setTimeout(() => {
      let message = "";
      compRef.props.liveStockBreedError
        ? (message = "Something went wrong !")
        : (message = displayMessage);
      Toaster.Toaster(message, compRef.props.liveStockBreedError);
      compRef.setCurrentMaterialToState(this.props.match.params.id);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    const liveStockMaterial = { ...this.state.liveStockMaterial };
    const category = [
      { label: "Adult", value: "Adult" },
      { label: "Child", value: "Child" }
    ];
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Material Form"
        navigation={true}
        navigationRoute="/liveStock/liveStockMaterials"
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="7">
              <FormGroup row>
                <Col xs="12" md="6">
                  <Label>Livestock name</Label>
                  <DropdownSelect
                    name="Livestock name"
                    label="Livestock same"
                    options={this.props.liveStockList}
                    value={liveStockMaterial.LiveStockId}
                    required={liveStockMaterial.LiveStockIdRequired}
                    onChange={this.onChangeLiveStock.bind(this)}
                  />
                </Col>
                <Col md="6">
                  <Label>Step name</Label>
                  <DropdownSelect
                    name="Step name"
                    label="Step name"
                    options={this.state.stepOptions}
                    value={liveStockMaterial.LiveStock_StepId}
                    disabled={this.state.stepDisabled}
                    required={liveStockMaterial.LiveStock_StepIdRequired}
                    onChange={this.onChangeLiveStockStep.bind(this)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="6">
                  <InputElement
                    type="text"
                    name="LiveMaterialName"
                    label="Material name"
                    maxLength={250}
                    placeholder="Material name"
                    value={liveStockMaterial.LiveMaterialName}
                    required={liveStockMaterial.LiveMaterialNameRequired}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
                <Col md="6">
                  <Label>Category</Label>
                  <DropdownSelect
                    name="Category"
                    label="Category"
                    options={category}
                    value={liveStockMaterial.Category}
                    required={liveStockMaterial.CategoryRequired}
                    onChange={this.onChangeCategory.bind(this)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="6">
                  <InputElement
                    type="text"
                    name="Cost"
                    label="Cost"
                    maxLength={10}
                    placeholder="Cost"
                    value={liveStockMaterial.Cost}
                    onKeyPress={e => this.setInputToNumeric(e)}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
                <Col md="6">
                  <InputElement
                    type="text"
                    name="Quantity"
                    label="Quantity"
                    maxLength={10}
                    placeholder="Quantity"
                    value={liveStockMaterial.Quantity}
                    onKeyPress={e => this.setInputToNumeric(e)}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12" md="6">
                  <InputElement
                    type="text"
                    name="Cost_measured"
                    label="Cost measured"
                    placeholder="Cost"
                    value={liveStockMaterial.Cost_measured}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
                <Col md="6">
                  <InputElement
                    type="text"
                    name="Quantity_measured"
                    label="Quantity measured"
                    placeholder="Quantity measured"
                    value={liveStockMaterial.Quantity_measured}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
              <FormGroup row>
                <Col xs="12">
                  <InputElement
                    type="textarea"
                    name="LiveMaterialDesc"
                    label="Material description"
                    placeholder="Material description"
                    value={liveStockMaterial.LiveMaterialDesc}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
            </Col>
            {/* <Col md="1"/> */}
            <Col md="5">
              {liveStockMaterial.renderURL ? (
                <img
                  src={liveStockMaterial.renderURL}
                  height={300}
                  width={400}
                  alt=""
                />
              ) : null}
            </Col>
          </FormGroup>

          {this.state.updateFlag ? (
            <div>
              <div style={{ marginTop: 0 }}>
                <CollapseCards
                  subName="Audio Allocation"
                  buttonName="Add Audio"
                  buttonLink={this}
                  buttonClick={this.onAddAudio.bind(this)}
                  isOpen={this.state.audioGridOpen}
                  toggleCollapse={this.audioToggleCollapse.bind(this)}
                >
                  <FormGroup row>
                    <Col xs="12">
                      <AudioAllocationGrid
                        audioAllocation={this.state.activeAudioAllocation}
                        playAudio={this.playAudio.bind(this)}
                        onDelete={this.onDeleteAudio.bind(this)}
                      />
                    </Col>
                  </FormGroup>
                </CollapseCards>
              </div>
              <div style={{ marginTop: -30 }}>
                <CollapseCards
                  subName="Image Allocation"
                  buttonName={
                    this.state.imageAllocation.length === 0 ? "Add Image" : null
                  }
                  buttonLink={this}
                  buttonClick={this.onAddImage.bind(this)}
                  isOpen={this.state.imageGridOpen}
                  toggleCollapse={this.imageToggleCollapse.bind(this)}
                >
                  <FormGroup row>
                    <Col xs="12">
                      <ImageAllocationGrid
                        imageAllocation={this.state.imageAllocation}
                        showImage={this.showImage.bind(this)}
                        onDelete={this.onDeleteImage.bind(this)}
                      />
                    </Col>
                  </FormGroup>
                </CollapseCards>
              </div>
            </div>
          ) : null}

          {this.state.updateFlag ? (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Save
                </Button>
              </Col>
            </FormGroup>
          ) : (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Create
                </Button>
              </Col>
              <Col md="1">
                <Button
                  className="theme-reset-btn"
                  onClick={() => this.onReset()}
                >
                  Reset
                </Button>
              </Col>
            </FormGroup>
          )}
        </div>
        {this.state.itemToDelete === "image" ? 
          <ConfirmModal
            isOpen={this.state.modalStatus}
            onModalToggle={this.onModalToggle.bind(this)}
            onConfirmDelete={this.onConfirmDeleteImage.bind(this)}
            title="Delete"
            message="Are you sure you want to remove this image ?"
          />
        : 
          <ConfirmModal
            isOpen={this.state.modalStatus}
            onModalToggle={this.onModalToggle.bind(this)}
            onConfirmDelete={this.onConfirmDeleteAudio.bind(this)}
            title="Delete"
            message="Are you sure you want to remove this audio ?"
          />
        }
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStockMaterial: state.liveStockReducer.activeLiveStockMaterial,
    liveStockMaterialError: state.liveStockReducer.liveStockMaterialError,
    liveStockList: state.liveStockReducer.liveStockList,
    liveStockStepsList: state.liveStockReducer.liveStockStepsList,
    imageFiles: state.mediaReducer.imageFiles,
    currentMaterialAudios: state.liveStockReducer.currentMaterialAudios
  };
};

const mapDispatchToProps = dispatch => {
  return {
    createLiveStockMaterial: liveStockMaterial =>
      dispatch(actions.createLiveStockMaterial(liveStockMaterial)),
    updateLiveStockMaterial: (id, liveStockMaterial) =>
      dispatch(actions.updateLiveStockMaterial(id, liveStockMaterial)),
    getLivestockMaterialAudioAllocations: id =>
      dispatch(actions.getLivestockMaterialAudioAllocations(id)),
    deactivateLivestockMaterialAudioAllocations: (id,audio) =>
      dispatch(actions.deactivateLivestockMaterialAudioAllocations(id,audio)),
      updateMaterialImage: image => dispatch(actions.updateMaterialImage(image))
      
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  LiveStockMaterialForm
);



