import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Row } from "reactstrap";
import Loader from "../../../components/Loader/Loader";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import * as constants from "../../../constants/StatusConstants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { Link } from "react-router-dom";
import AppConfig from "../../../constants/AppConfig";
import ActiveLiveStockMaterialTable from "./ActiveLiveStockMaterialTable";
import InActiveLiveStockMaterialTable from "./InActiveLiveStockMaterialTable";
class LivestockMaterial extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      tableStatus: true,
      modalStatus: false,
      materialToDelete: {},
      messageModelStatus: false,
      message: ""
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        loading: false
      });
    }, 1000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  onDeleteLiveStockMaterial(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  onEditLiveStockMaterial(cell, row) {
    return (
      <Link to={`${this.props.match.url}/liveStockMaterialForm/${row.Id}`}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  showImage(cell, row) {
    if (row.ImageURL) {
      return (
        <img
          src={`${AppConfig.serverURL}/${row.ImageURL}`}
          style={{ height: 50, width: 50 }}
          alt=""
        />
      );
    } else {
      return;
    }
  }

  onModalToggle(liveStockMaterial) {
    this.setState({
      materialToDelete: liveStockMaterial,
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
      let materialToDelete = { ...this.state.materialToDelete };
      let message = "";
      materialToDelete.ActiveBy = localStorage.getItem("user");
      if (this.state.tableStatus) {
        materialToDelete.Active = false;
        message = "Livestock step material deactivated successfully";
      } else {
        materialToDelete.Active = true;
        message = "Livestock step material activated successfully";
      }
      this.props.deactivateLiveStockMaterial(materialToDelete.Id, materialToDelete);
      this.setState({ loading: true });
      let compRef = this;
      setTimeout(() => {
        compRef.props.liveStockMaterialError
          ? (message = compRef.props.liveStockMaterialError)
          : message;
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.liveStockMaterialError);
      }, 2000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Materials"
        buttonName="Add Livestock Material"
        buttonLink={`${this.props.match.url}/liveStockMaterialForm`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveLiveStockMaterialTable
                materials={this.props.activeLiveStockMaterial}
                onDelete={this.onDeleteLiveStockMaterial.bind(this)}
                onEdit={this.onEditLiveStockMaterial.bind(this)}
                //showImage={this.showImage.bind(this)}
              />
            ) : (
              <InActiveLiveStockMaterialTable
                materials={this.props.inActiveLiveStockMaterial}
                onDelete={this.onDeleteLiveStockMaterial.bind(this)}
                //showImage={this.showImage.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this livestock material record ?"
              : "Are you sure you want to activate this livestock material record ?"
          }
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStockMaterial: state.liveStockReducer.activeLiveStockMaterial,
    inActiveLiveStockMaterial: state.liveStockReducer.inActiveLiveStockMaterial,
    liveStockMaterialsList: state.liveStockReducer.liveStockMaterialsList,
    liveStockMaterialError: state.liveStockReducer.liveStockMaterialError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    deactivateLiveStockMaterial: (id, liveStockMaterial) =>
      dispatch(actions.deactivateLiveStockMaterial(id, liveStockMaterial))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(LivestockMaterial);



