import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Button, Row, Label } from "reactstrap";
import _ from "lodash";
import Loader from "../../../components/Loader/Loader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import InputElement from "../../../components/InputElement/InputElement";
import AppConfig from "../../../constants/AppConfig";
import CollapseCards from "../../../components/Cards/CollapseCards";
import AudioAllocationGrid from "../AudioAllocationGrid";
import AudioPlayer from "../../../components/AudioPlayer/AudioPlayer";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import { Link } from "react-router-dom";
import ImageAllocationGrid from "../ImageAllocationGrid";
class LiveStockBreedForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      liveStockBreed: {
        Id: "",
        LiveStockId: "",
        StockName: "",
        BreedName: "",
        Active: true,
        CreatedOn: "",
        CreatedBy: "",
        UpdatedBy: "",
        UpdatedOn: "",
        ImageURL: "",
        StockNameRequired: false,
        BreedNameRequired: false,
        renderURL: ""
      },
      audioGridOpen : false,
      activeAudioAllocation: [],
      imageGridOpen:false,
      imageAllocation :[],
      modalStatus: false,
      itemToDelete: "",
      audioToDelete: {},
    };
  }

  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      if (this.props.activeLiveStockBreed.length !== 0) {
        let id = this.props.match.params.id;
        this.setCurrentLivestockBreedToState(id);
      }
      // setTimeout(() => {
      //   this.setState({
      //     loading: false,
      //     updateFlag: true
      //   });
      // }, 2000);
    } else {
      setTimeout(() => {
        this.setState({
          loading: false
        });
      }, 1000);
    }
    if (this.props.liveStockBreedError) {
      Toaster.Toaster("Something went wrong !", this.props.liveStockBreedError);
    }
  }
  setCurrentLivestockBreedToState(id) {
    this.props.getBreedAudioAllocations(id);
    let liveStockBreed = _.find(this.props.activeLiveStockBreed, function(
      breed
    ) {
      return breed.Id === parseInt(id);
    });
    if (liveStockBreed !== undefined) {
      if (liveStockBreed.ImageURL) {
        liveStockBreed.renderURL = `${AppConfig.serverURL}/${liveStockBreed.ImageURL}`;
      }
      let imageAllocation = _.filter(this.props.imageFiles, function(image){
        return (image.FilePath === liveStockBreed.renderURL || image.ServerPath === liveStockBreed.ImageURL)
      });
      // let imageAllocation = _.filter(this.props.imageFiles, {
      //   FilePath: liveStockBreed.renderURL
      // });
      setTimeout(() => {
          let activeAudioAllocation = _.filter(
            this.props.currentBreedAudios,
            { Active: true }
          );
        this.setState({
          updateFlag: true,
          liveStockBreed: liveStockBreed,
          loading: false,
          activeAudioAllocation: activeAudioAllocation,
          audioGridOpen: true,
          imageGridOpen: true,
          imageAllocation: imageAllocation,
        });
      }, 3000);
    } else {
      this.props.history.push("/liveStock/liveStockBreeds");
    }
  }
  onChangeName(event) {
    let liveStockBreed = { ...this.state.liveStockBreed };
    liveStockBreed[event.target.name] = event.target.value;
    liveStockBreed[event.target.name + "Required"] = false;
    this.setState({ liveStockBreed });
  }
  onChangeStock(value) {
    let liveStockBreed = { ...this.state.liveStockBreed };
    liveStockBreed.LiveStockId = value;
    liveStockBreed.StockNameRequired = false;
    this.setState({ liveStockBreed });
  }
  onSubmit() {
    let liveStockBreed = { ...this.state.liveStockBreed };
    liveStockBreed.BreedName = liveStockBreed.BreedName.trim();
    if (
      liveStockBreed.BreedName &&
      liveStockBreed.BreedName.trim().length > 0 &&
      liveStockBreed.LiveStockId
    ) {
      if (this.state.updateFlag) {
        let liveStockBreedData = _.pick(liveStockBreed, [
          "Id",
          "LiveStockId",
          "BreedName",
          "UpdatedBy",
          "UpdatedOn",
          "Active"
        ]);
        liveStockBreedData.UpdatedBy = localStorage.getItem("user");
        liveStockBreedData.UpdatedOn = new Date();
        liveStockBreedData.Active = true;
        this.props.updateLiveStockBreed(
          liveStockBreedData.Id,
          liveStockBreedData
        );
      } else {
        let liveStockBreedData = _.pick(liveStockBreed, [
          "LiveStockId",
          "BreedName",
          "CreatedBy",
          "CreatedOn",
          "Active"
        ]);
        liveStockBreedData.CreatedBy = localStorage.getItem("user");
        liveStockBreedData.CreatedOn = new Date();
        liveStockBreedData.Active = true;
        this.props.createLiveStockBreed(liveStockBreedData);
      }
      this.setState({ loading: true });
      let message = "";
      let compRef = this;
      setTimeout(() => {
        compRef.props.liveStockBreedError
          ? (message = compRef.props.liveStockBreedError)
          : compRef.state.updateFlag
            ? (message = "Livestock breed updated successfully")
            : (message = "Livestock breed created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.liveStockBreedError);
        setTimeout(() => {
          if (!compRef.props.liveStockBreedError) {
            compRef.onReset();
            compRef.props.history.push("/liveStock/liveStockBreeds");
          }
        }, 1000);
      }, 1000);
    } else {
      if (!liveStockBreed.LiveStockId) liveStockBreed.StockNameRequired = true;
      if (
        !liveStockBreed.BreedName ||
        liveStockBreed.BreedName.trim().length <= 0
      )
        liveStockBreed.BreedNameRequired = true;
      this.setState({ liveStockBreed: liveStockBreed });
    }
  }
  onReset() {
    this.setState({
      liveStockBreed: {
        Id: "",
        LiveStockId: "",
        StockName: "",
        BreedName: "",
        Active: true,
        CreatedOn: "",
        CreatedBy: "",
        UpdatedBy: "",
        UpdatedOn: "",
        ImageURL: "",
        StockNameRequired: false,
        BreedNameRequired: false,
        renderURL: ""
      }
    });
  }
   /**--------------AUDIO ALLOCATION FUNCTIONS----------------- */
   onModalToggle(itemToDelete) {
    this.setState({
      itemToDelete: itemToDelete,
      modalStatus: !this.state.modalStatus
    });
  }
  audioToggleCollapse() {
    this.setState({
      audioGridOpen: !this.state.audioGridOpen
    });
  }
  onAddAudio(){
    if (this.props.match.params.id !== undefined) {
      this.props.history.push(
        `/liveStock/audioAllocation/${"liveStockBreed"}/${this.props.match.params
          .id}`
      );
    }
  }
  playAudio(cell, row) {
    return <AudioPlayer autoPlay={false} source={row.FilePath} />;
  }
  onDeleteAudio(cell, row) {
    return (
      <Link to={this} onClick={() => this.deleteAudio(row)}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }
  deleteAudio(row) {
    this.setState({ audioToDelete: row });
    this.onModalToggle("audio");
  }
  onConfirmDeleteAudio() {
    let audioToDelete = { ...this.state.audioToDelete };
    let compRef = this;
    audioToDelete.Active = false;
    audioToDelete.ActiveBy = localStorage.getItem("user");
    audioToDelete.ActiveOn = new Date();
    this.props.deactivateBreedAudioAllocations(audioToDelete.Id, audioToDelete);
    let displayMessage = "Livestock breed audio removed successfully";
    setTimeout(() => {
      let message = "";
      compRef.props.liveStockBreedError
        ? (message = "Something went wrong !")
        : (message = displayMessage);
      Toaster.Toaster(message, compRef.props.liveStockBreedError);
      compRef.setCurrentLivestockBreedToState(this.props.match.params.id);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
   audioToggleCollapse() {
    this.setState({
      audioGridOpen: !this.state.audioGridOpen
    });
  }
  
    /**------------------------Image Allocation functions ---------------------*/
    imageToggleCollapse() {
      this.setState({
        imageGridOpen: !this.state.imageGridOpen
      });
    }
    onAddImage() {
      if (this.props.match.params.id !== undefined) {
        this.props.history.push(
          `/liveStock/imageAllocation/${"liveStockBreed"}/${this.props.match.params
            .id}`
        );
      }
    }
  showImage(cell, row) {
    if (row.FilePath) {
      return (
        <img src={row.FilePath} style={{ height: 50, width: 50 }} alt="" />
      );
    } else {
      return;
    }
  }
  onDeleteBreedImage(cell, row) {
    return (
      <Link to={this} onClick={() => this.onModalToggle("image")}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }
  onConfirmDeleteImage() {
    let liveStockBreed = { ...this.state.liveStockBreed };
    //this.setState({ notificationItem: "image" });
    let compRef = this;
    liveStockBreed.UpdatedBy = localStorage.getItem("user");
    liveStockBreed.UpdatedOn = new Date();
    liveStockBreed.ImageURL = "";
    let image = _.pick(liveStockBreed, [
      "Id",
      "ImageURL",
      "UpdatedBy",
      "UpdatedOn"
    ]);
    this.props.updateBreedImage(image);
    let displayMessage = "Livestock breed image removed successfully";
    setTimeout(() => {
      let message = "";
      compRef.props.liveStockBreedError
        ? (message = "Something went wrong !")
        : (message = displayMessage);
      Toaster.Toaster(message, compRef.props.liveStockBreedError);
      compRef.setCurrentLivestockBreedToState(this.props.match.params.id);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    const liveStockBreed = { ...this.state.liveStockBreed };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Breed Form"
        navigation={true}
        navigationRoute="/liveStock/liveStockBreeds"
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="7">
              <FormGroup row>
                <Col xs="12" md="6">
                  <Label>Livestock name</Label>
                  <DropdownSelect
                    label="Livestock name"
                    name="Livestock name"
                    placeholder="Select Livestock"
                    options={this.props.liveStockList}
                    value={liveStockBreed.LiveStockId}
                    required={liveStockBreed.StockNameRequired}
                    onChange={event => this.onChangeStock(event)}
                  />
                </Col>
                <Col md="6">
                  <InputElement
                    type="text"
                    name="BreedName"
                    label="Breed name"
                    maxLength={250}
                    placeholder="Breed name"
                    value={liveStockBreed.BreedName}
                    required={liveStockBreed.BreedNameRequired}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
            </Col>
            <Col md="5">
              {liveStockBreed.renderURL ? (
                <img
                  src={liveStockBreed.renderURL}
                  height={300}
                  width={350}
                  alt=""
                />
              ) : null}
            </Col>
          </FormGroup>

          {this.state.updateFlag ? (
              <div>
                <div style={{ marginTop: 0 }}>
                  <CollapseCards
                    subName="Audio Allocation"
                    buttonName="Add Audio"
                    buttonLink={this}
                    buttonClick={this.onAddAudio.bind(this)}
                    isOpen={this.state.audioGridOpen}
                    toggleCollapse={this.audioToggleCollapse.bind(this)}
                  >
                    <FormGroup row>
                      <Col xs="12">
                        <AudioAllocationGrid
                          audioAllocation={this.state.activeAudioAllocation}
                          playAudio={this.playAudio.bind(this)}
                          onDelete={this.onDeleteAudio.bind(this)}
                        />
                      </Col>
                    </FormGroup>
                  </CollapseCards>
                </div>
                <div style={{ marginTop: -30 }}>
                  <CollapseCards
                    subName="Image Allocation"
                    buttonName={
                      this.state.imageAllocation.length === 0
                        ? "Add Image"
                        : null
                    }
                    buttonLink={this}
                    buttonClick={this.onAddImage.bind(this)}
                    isOpen={this.state.imageGridOpen}
                    toggleCollapse={this.imageToggleCollapse.bind(this)}
                  >
                    <FormGroup row>
                      <Col xs="12">
                        <ImageAllocationGrid
                          imageAllocation={this.state.imageAllocation}
                          showImage={this.showImage.bind(this)}
                          onDelete={this.onDeleteBreedImage.bind(this)}
                        />
                      </Col>
                    </FormGroup>
                  </CollapseCards>
                </div>
              </div>
            ) : null} 

          {this.state.updateFlag ? (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Save
                </Button>
              </Col>
            </FormGroup>
          ) : (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Create
                </Button>
              </Col>
              <Col md="1">
                <Button
                  className="theme-reset-btn"
                  onClick={() => this.onReset()}
                >
                  Reset
                </Button>
              </Col>
            </FormGroup>
          )}
        </div>
        {this.state.itemToDelete === "image" ? 
          <ConfirmModal
            isOpen={this.state.modalStatus}
            onModalToggle={this.onModalToggle.bind(this)}
            onConfirmDelete={this.onConfirmDeleteImage.bind(this)}
            title="Delete"
            message="Are you sure you want to remove this image ?"
          />
        : 
          <ConfirmModal
            isOpen={this.state.modalStatus}
            onModalToggle={this.onModalToggle.bind(this)}
            onConfirmDelete={this.onConfirmDeleteAudio.bind(this)}
            title="Delete"
            message="Are you sure you want to remove this audio ?"
          />
        }
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStockBreed: state.liveStockReducer.activeLiveStockBreed,
    inActiveLiveStockBreed: state.liveStockReducer.inActiveLiveStockBreed,
    liveStockList: state.liveStockReducer.liveStockList,
    liveStockBreedError: state.liveStockReducer.liveStockBreedError,
    imageFiles: state.mediaReducer.imageFiles,
    currentBreedAudios: state.liveStockReducer.currentBreedAudios
  };
};

const mapDispatchToProps = dispatch => {
  return {
    createLiveStockBreed: liveStockBreed =>
      dispatch(actions.createLiveStockBreed(liveStockBreed)),
    updateLiveStockBreed: (id, liveStockBreed) =>
      dispatch(actions.updateLiveStockBreed(id, liveStockBreed)),
    getBreedAudioAllocations : id => dispatch(actions.getBreedAudioAllocations(id)),
    deactivateBreedAudioAllocations : (id, audio) => dispatch(actions.deactivateBreedAudioAllocations(id, audio)),
    updateBreedImage: (image) => dispatch(actions.updateBreedImage(image))
    
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(LiveStockBreedForm);



