import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class InActiveBreedCategoryTable extends Component {
  render() {
    const sortingOptions = {
      defaultSortName: "CategoryName",
      noDataText: "No records found for livestock breed category",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.breedCategory.length
        }
      ],
      sizePerPage: 5
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.breedCategory}
        options={sortingOptions}
        search={true}
        pagination={this.props.breedCategory.length > 0 ? true : false}
        hover={true}
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="StockName"
          headerAlign="left"
          width="20"
          dataSort={true}
        >
          Livestock Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="BreedName"
          headerAlign="left"
          width="20"
          dataSort={true}
        >
          Breed Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="CategoryName"
          headerAlign="left"
          width="20"
          dataSort={true}
        >
          Category Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="delete"
          dataFormat={this.props.onDelete}
          headerAlign="left"
          width="20"
          export={false}
        >
          Activate
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

export default InActiveBreedCategoryTable;



