import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class ActiveLiveStockTable extends Component {
  render() {
    const sortingOptions = {
      defaultSortName: "StockName",
      noDataText: "No records found for livestock ",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.liveStocks.length
        }
      ],
      sizePerPage: 5
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.liveStocks}
        options={sortingOptions}
        search={true}
        pagination={this.props.liveStocks.length > 0 ? true : false}
        hover={true}
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="StockName"
          headerAlign="left"
          width="20"
          dataSort={true}
        >
        Livestock Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="FilePath"
          headerAlign="left"
          width="40"
          dataFormat={this.props.showImage}
          dataSort={true}
        >
          Image
        </TableHeaderColumn>
        <TableHeaderColumn 
        width="20"
        dataField="edit" 
        dataFormat={this.props.onEdit}
        headerAlign="left" >
        Edit
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="delete"
          dataFormat={this.props.onDelete}
          headerAlign="left"
          width="20"
          export={false}
        >
          Deactivate
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

export default ActiveLiveStockTable;



