import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Row } from "reactstrap";
import Loader from "../../../components/Loader/Loader";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import * as constants from "../../../constants/StatusConstants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { Link } from "react-router-dom";
import ActiveStepTable from "./ActiveStepTable";
import InActiveStepTable from "./InActiveStepTable";

class LivestockSteps extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      tableStatus: true,
      modalStatus: false,
      liveStockStepToDelete: {},
      messageModelStatus: false,
      message: ""
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        loading: false
      });
    }, 1000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  onDeleteLiveStockStep(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  onEditLiveStockStep(cell, row) {
    return (
      <Link to={`${this.props.match.url}/liveStockStepForm/${row.Id}`}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  onModalToggle(liveStockStep) {
    this.setState({
      liveStockStepToDelete: liveStockStep,
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let liveStockStepToDelete = { ...this.state.liveStockStepToDelete };
    let message = "";
    liveStockStepToDelete.ActiveBy = localStorage.getItem("user");
    liveStockStepToDelete.ActiveOn = new Date();
    if (this.state.tableStatus) {
      liveStockStepToDelete.Active = false;
      message = "Livestock step deactivated successfully";
    } else {
      liveStockStepToDelete.Active = true;
      message = "Livestock step activated successfully";
    }
    this.props.deactivateLiveStockStep(liveStockStepToDelete.Id, liveStockStepToDelete);
    this.setState({ loading: true });
    let compRef = this;
    setTimeout(() => {
      compRef.props.liveStockStepError
        ? (message = compRef.props.liveStockStepError)
        : message;
      compRef.setState({ loading: false });
      Toaster.Toaster(message, compRef.props.liveStockStepError);
    }, 2000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Steps"
        buttonName="Add Livestock Steps"
        buttonLink={`${this.props.match.url}/liveStockStepForm`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveStepTable
                steps={this.props.activeLiveStockStep}
                onDelete={this.onDeleteLiveStockStep.bind(this)}
                onEdit={this.onEditLiveStockStep.bind(this)}
              />
            ) : (
              <InActiveStepTable
                steps={this.props.inActiveLiveStockStep}
                onDelete={this.onDeleteLiveStockStep.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this livestock step record ?"
              : "Are you sure you want to activate this livestock step record ?"
          }
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStockStep: state.liveStockReducer.activeLiveStockStep,
    inActiveLiveStockStep: state.liveStockReducer.inActiveLiveStockStep,
    liveStockStepError: state.liveStockReducer.liveStockStepError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getLiveStock: () => dispatch(actions.getLiveStocks()),
    deactivateLiveStockStep: (id, liveStockStep) =>
      dispatch(actions.deactivateLiveStockStep(id, liveStockStep))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(LivestockSteps);



