import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col,  Row } from "reactstrap";
import Loader from "../../../components/Loader/Loader";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import * as constants from "../../../constants/StatusConstants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { Link } from "react-router-dom";
import ActiveLiveStockBreedTable from "./ActiveLiveStockBreedTable";
import InActiveLiveStockBreedTable from "./InActiveLiveStockBreedTable";

class LivestockBreed extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      tableStatus: true,
      modalStatus: false,
      liveStockBreedToDelete: {},
      messageModelStatus: false,
      message: ""
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        loading: false
      });
    }, 1000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  onDeleteLiveStockBreed(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  onEditLiveStockBreed(cell, row) {
    return (
      <Link to={`${this.props.match.url}/liveStockBreedForm/${row.Id}`}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }

  onModalToggle(liveStockBreed) {
    this.setState({
      liveStockBreedToDelete: liveStockBreed,
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let liveStockBreedToDelete = { ...this.state.liveStockBreedToDelete };
    let message = "";
    liveStockBreedToDelete.ActiveBy = localStorage.getItem("user");
    if (this.state.tableStatus) {
      liveStockBreedToDelete.Active = false;
      message = "Livestock deactivated successfully";
    } else {
      liveStockBreedToDelete.Active = true;
      message = "Livestock activated successfully";
    }
    this.props.deactivateLiveStockBreed(
      liveStockBreedToDelete.Id,
      liveStockBreedToDelete
    );
    this.setState({ loading: true });
    let compRef = this;
    setTimeout(() => {
      compRef.props.liveStockBreedError
        ? (message = compRef.props.liveStockBreedError)
        : message;
      compRef.setState({ loading: false });
      Toaster.Toaster(message, compRef.props.liveStockBreedError);
    }, 2000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Breeds"
        buttonName="Add Livestock Breed"
        buttonLink={`${this.props.match.url}/liveStockBreedForm`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveLiveStockBreedTable
                liveStockBreeds={this.props.activeLiveStockBreed}
                onDelete={this.onDeleteLiveStockBreed.bind(this)}
                onEdit={this.onEditLiveStockBreed.bind(this)}
              />
            ) : (
              <InActiveLiveStockBreedTable
                liveStockBreeds={this.props.inActiveLiveStockBreed}
                onDelete={this.onDeleteLiveStockBreed.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this livestock record ?"
              : "Are you sure you want to activate this livestock record ?"
          }
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStockBreed: state.liveStockReducer.activeLiveStockBreed,
    inActiveLiveStockBreed: state.liveStockReducer.inActiveLiveStockBreed,
    liveStockBreedError: state.liveStockReducer.liveStockBreedError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getLiveStock: () => dispatch(actions.getLiveStocks()),
    deactivateLiveStockBreed: (id, liveStockBreed) =>
      dispatch(actions.deactivateLiveStockBreed(id, liveStockBreed))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(LivestockBreed);



