import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import Loader from "../../components/Loader/Loader";
import { FormGroup, Col, Button, Label } from "reactstrap";
import CardLayout from "../../components/Cards/CardLayout";
import _ from "lodash";
import { Async } from "react-select";
import "react-select/dist/react-select.css";
import AppConfig from "../../constants/AppConfig";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import NotificationModal from "./NotificationModal";

class ImageAllocationForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      imageAllocation: {
        imageRequired: false
      },
      cardTitle: "",
      imageCategory: "",
      imageValue: "",
      renderURL: "",
      error: "",
      notificationFlag: false
    };
  }
  componentDidMount() {
    let imageCategory = this.props.match.params.imageCategory;
    setTimeout(() => {
      let id = parseInt(this.props.match.params.id);
      let imageAllocation = {};
      if (imageCategory === "liveStocks") {
        imageAllocation = _.find(this.props.activeLiveStock, function(item) {
          return item.Id === id;
        });
      } else if (imageCategory === "liveStockBreed") {
        imageAllocation = _.find(this.props.activeLiveStockBreed, function(
          item
        ) {
          return item.Id === id;
        });
      } else if (imageCategory === "liveStockBreedCategory") {
        imageAllocation = _.find(this.props.activeBreedCategory, function(
          item
        ) {
          return item.Id === id;
        });
      } else if (imageCategory === "liveStockStep") {
        imageAllocation = _.find(this.props.activeLiveStockStep, function(
          item
        ) {
          return item.Id === id;
        });
      } else if (imageCategory === "liveStockMaterial") {
        imageAllocation = _.find(this.props.activeLiveStockMaterial, function(
          item
        ) {
          return item.Id === id;
        });
      } else {
        this.goBack();
      }
      if (imageAllocation !== undefined) {
        imageAllocation.imageRequired = false;
        this.setState({
          loading: false,
          imageAllocation
        });
      } else {
        this.goBack();
      }
    }, 2000);
  }
  handleImageChange = imageSelected => {
    let imageAllocation = { ...this.state.imageAllocation };
    if (imageSelected !== null) {
      imageAllocation.ImageURL = imageSelected.FilePath;
      imageAllocation.imageRequired = false;
      let imageValue = imageSelected;
      let renderURL = `${AppConfig.serverURL}/${imageSelected.FilePath}`;
      this.setState({ imageAllocation, renderURL, imageValue });
    } else {
      imageAllocation.FilePath = "";
      this.setState({ imageAllocation, renderURL: "", imageValue: "" });
    }
  };
  goBack() {
    this.props.history.goBack();
  }
  onSubmit() {
    let imageAllocation = { ...this.state.imageAllocation };
    if (imageAllocation.ImageURL) {
      imageAllocation.UpdatedBy = localStorage.getItem("user");
      imageAllocation.UpdatedOn = new Date();
      let image = _.pick(imageAllocation, [
        "Id",
        "ImageURL",
        "UpdatedBy",
        "UpdatedOn"
      ]);
      let imageCategory = this.props.match.params.imageCategory;
      if (imageCategory === "liveStocks") {
        this.props.updateLivestockImage(image);
      } else if (imageCategory === "liveStockBreed") {
        this.props.updateBreedImage(image);
      } else if (imageCategory === "liveStockBreedCategory") {
        //get liveStockBreedCategory
      } else if (imageCategory === "liveStockStep") {
        this.props.updateStepImage(image);
      } else if (imageCategory === "liveStockMaterial") {
        this.props.updateMaterialImage(image);
      } else {
        this.goBack();
      }
      let message = "";
      let compRef = this;
      this.setState({ loading: true });
      if (imageCategory === "liveStocks") {
        if (this.props.liveStockError) {
          this.setState({ error: this.props.liveStockError });
        } else {
          this.setState({ error: "" });
        }
      } else if (imageCategory === "liveStockBreed") {
        if (this.props.liveStockBreedError) {
          this.setState({ error: this.props.liveStockBreedError });
        } else {
          this.setState({ error: "" });
        }
      } else if (imageCategory === "liveStockBreedCategory") {
        //get liveStockBreedCategory
      } else if (imageCategory === "liveStockStep") {
        if (this.props.liveStockStepError) {
          this.setState({ error: this.props.liveStockStepError });
        } else {
          this.setState({ error: "" });
        }
      } else if (imageCategory === "liveStockMaterial") {
        if (this.props.liveStockMaterialError) {
          this.setState({ error: this.props.liveStockMaterialError });
        } else {
          this.setState({ error: "" });
        }
      } else {
        this.setState({ error: "" });
      }
      setTimeout(() => {
        compRef.state.error
          ? (message = "Something went wrong !")
          : (message = "Image allocation created successfully");
        compRef.onReset();
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.state.error);
        compRef.onReset();
        setTimeout(() => {
          if (!compRef.state.error) {
            //compRef.goBack();
            compRef.onNotificationToggle();
          }else{
            compRef.goBack();
          }
        }, 2000);
      }, 1000);
    } else {
      if (!imageAllocation.ImageURL) imageAllocation.imageRequired = true;
      this.setState({ imageAllocation });
    }
  }
  onReset() {
    let imageAllocation = { ...this.state.imageAllocation };
    imageAllocation.imageRequired = false;
    imageAllocation.FilePath = "";
    this.setState({
      imageAllocation,
      imageValue: "",
      renderURL: ""
    });
  }
  onNotificationToggle(){
    this.setState({ notificationFlag : !this.state.notificationFlag});
  }
  render() {
    const audioAllocation = { ...this.state.audioAllocation };
    const options = this.props.imageOptions;
    const getOptions = (input, callback) => {
      let filterOpt = [];
      if (input) {
        filterOpt = _.filter(options, function(image) {
          if (
            _.includes(image.label.toLowerCase(), input.toLowerCase()) ||
            image.label.toLowerCase() == input.toLowerCase()
          ) {
            return image;
          }
        });
      }
      setTimeout(() => {
        callback(null, {
          options: filterOpt,
          complete: true
        });
      }, 500);
    };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Image Allocation Form"
        navigation={true}
        navigationRoute={this}
        onClick={this.goBack.bind(this)}
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="6">
              <FormGroup row>
                <Col xs="12" md="10">
                  <Label>Image</Label>
                  <Async
                    name="form-field-name"
                    cacheOptions
                    value={this.state.imageValue}
                    defaultOptions
                    onChange={this.handleImageChange.bind(this)}
                    loadOptions={getOptions}
                  />
                  {this.state.imageAllocation.imageRequired ? (
                    <div className="help-block">*Image is required</div>
                  ) : null}
                </Col>
              </FormGroup>
              <FormGroup row />
              <FormGroup row>
                <Col xs="12" md="2">
                  <Button
                    className="theme-positive-btn"
                    onClick={this.onSubmit.bind(this)}
                  >
                    Create
                  </Button>
                </Col>
                <Col md="2">
                  <Button
                    className="theme-reset-btn"
                    onClick={this.onReset.bind(this)}
                  >
                    Reset
                  </Button>
                </Col>
              </FormGroup>
            </Col>
            {this.state.renderURL ? (
              <Col md="6" className="image-display">
                <img
                  src={this.state.renderURL}
                  height={300}
                  width={450}
                  alt=""
                />
              </Col>
            ) : null}
          </FormGroup>
        </div>
        <NotificationModal
          active={true}
          category="image"
          notificationContext={this.props.match.params.imageCategory}
          contextId={parseInt(this.props.match.params.id)}
          openFlag={this.state.notificationFlag}
          toggleFunction={this.onNotificationToggle.bind(this)}
          shouldGoBack={true}
          fieldType={""}
          {...this.props}
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}
export const mapStateToProps = state => {
  return {
    imageOptions: state.mediaReducer.imageOptions,
    imageFiles: state.mediaReducer.imageFiles,
    activeLiveStock: state.liveStockReducer.activeLiveStock,
    activeLiveStockBreed: state.liveStockReducer.activeLiveStockBreed,
    activeBreedCategory: state.liveStockReducer.activeBreedCategory,
    activeLiveStockMaterial: state.liveStockReducer.activeLiveStockMaterial,
    activeLiveStockStep: state.liveStockReducer.activeLiveStockStep,
    liveStockError: state.liveStockReducer.liveStockError,
    liveStockBreedError: state.liveStockReducer.liveStockBreedError,
    liveStockStepError: state.liveStockReducer.liveStockStepError,
    liveStockMaterialError: state.liveStockReducer.liveStockMaterialError    
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    updateLivestockImage: image =>
      dispatch(actions.updateLivestockImage(image)),
    updateBreedImage: image => dispatch(actions.updateBreedImage(image)),
    updateStepImage: image => dispatch(actions.updateStepImage(image)),
    updateMaterialImage: image => dispatch(actions.updateMaterialImage(image))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  ImageAllocationForm
);



