import React, { Component } from "react";
import { BootstrapTable, TableHeaderColumn } from "react-bootstrap-table";
import "react-bootstrap-table/dist/react-bootstrap-table.min.css";
class ActiveStepTable extends Component {
  render() {
    const sortingOptions = {
      defaultSortName: "StepName",
      noDataText: "No records found for livestock steps",
      defaultSortOrder: "asc",
      sizePerPageList: [
        {
          text: "5",
          value: 5
        },
        {
          text: "10",
          value: 10
        },
        {
          text: "20",
          value: 20
        },
        {
          text: "All",
          value: this.props.steps.length
        }
      ],
      sizePerPage: 5
    };
    return (
      <BootstrapTable
        ref="table"
        data={this.props.steps}
        options={sortingOptions}
        search={true}
        pagination={this.props.steps.length > 0 ? true : false}
        hover={true}
      >
        <TableHeaderColumn dataField="Id" headerAlign="left" isKey hidden>
          Id
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="StockName"
          headerAlign="left"
          width="20"
          dataSort={true}
        >
          Livestock Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="StepName"
          headerAlign="left"
          width="20"
          dataSort={true}
        >
          Step Name
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="StepDescription"
          headerAlign="left"
          width="40"
          dataSort={true}
        >
          Step Description
        </TableHeaderColumn>
        <TableHeaderColumn
          width="20"
          dataField="edit"
          dataFormat={this.props.onEdit}
          headerAlign="left"
        >
          Edit
        </TableHeaderColumn>
        <TableHeaderColumn
          dataField="delete"
          dataFormat={this.props.onDelete}
          headerAlign="left"
          width="20"
          export={false}
        >
          Deactivate
        </TableHeaderColumn>
      </BootstrapTable>
    );
  }
}

export default ActiveStepTable;



