import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Row } from "reactstrap";
import Loader from "../../../components/Loader/Loader";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import * as constants from "../../../constants/StatusConstants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { Link } from "react-router-dom";
import AppConfig from "../../../constants/AppConfig";
import ActiveBreedCategoryTable from "./ActiveBreedCategoryTable";
import InActiveBreedCategoryTable from "./InActiveBreedCategoryTable";

class LivestockBreedCategory extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      tableStatus: true,
      modalStatus: false,
      breedCategoryToDelete: {},
      messageModelStatus: false,
      message: ""
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        loading: false
      });
    }, 1000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  onDeleteBreedCategory(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  onEditBreedCategory(cell, row) {
    return (
      <Link to={`${this.props.match.url}/liveStockBreedCategoryForm/${row.Id}`}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  showImage(cell, row) {
    if (row.ImageURL) {
      return (
        <img
          src={`${AppConfig.serverURL}/${row.ImageURL}`}
          style={{ height: 50, width: 50 }}
          alt=""
        />
      );
    } else {
      return;
    }
  }

  onModalToggle(breedCategory) {
    this.setState({
      breedCategoryToDelete: breedCategory,
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let breedCategoryToDelete = { ...this.state.breedCategoryToDelete };
    let message = "";
    breedCategoryToDelete.ActiveBy = localStorage.getItem("user");
    if (this.state.tableStatus) {
      breedCategoryToDelete.Active = false;
      message = "Livestock breed category deactivated successfully";
    } else {
      breedCategoryToDelete.Active = true;
      message = "Livestock breed category activated successfully";
    }
    this.props.deactivateBreedCategory(
      breedCategoryToDelete.Id,
      breedCategoryToDelete
    );
    this.setState({ loading: true });
    let compRef = this;
    setTimeout(() => {
      compRef.props.breedCategoryError
        ? (message = compRef.props.breedCategoryError)
        : message;
      compRef.setState({ loading: false });
      Toaster.Toaster(message, compRef.props.breedCategoryError);
    }, 2000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Breed Category"
        buttonName="Add Breed Category"
        buttonLink={`${this.props.match.url}/liveStockBreedCategoryForm`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveBreedCategoryTable
                breedCategory={this.props.activeBreedCategory}
                onDelete={this.onDeleteBreedCategory.bind(this)}
                onEdit={this.onEditBreedCategory.bind(this)}
                //showImage={this.showImage.bind(this)}
              />
            ) : (
              <InActiveBreedCategoryTable
                breedCategory={this.props.inActiveBreedCategory}
                onDelete={this.onDeleteBreedCategory.bind(this)}
                //showImage={this.showImage.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this livestock record ?"
              : "Are you sure you want to activate this livestock record ?"
          }
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeBreedCategory: state.liveStockReducer.activeBreedCategory,
    inActiveBreedCategory: state.liveStockReducer.inActiveBreedCategory,
    breedCategoryError: state.liveStockReducer.breedCategoryError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    // getLiveStock: () => dispatch(actions.getLiveStocks()),
    deactivateBreedCategory: (id, breedCategory) =>
      dispatch(actions.deactivateBreedCategory(id, breedCategory))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  LivestockBreedCategory
);



