import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import CollapseCards from "../../../components/Cards/CollapseCards";
import { FormGroup, Col, Button } from "reactstrap";
import _ from "lodash";
import Loader from "../../../components/Loader/Loader";
import { Link } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import InputElement from "../../../components/InputElement/InputElement";
import AppConfig from "../../../constants/AppConfig";
import AudioAllocationGrid from "../AudioAllocationGrid";
import ImageAllocationGrid from "../ImageAllocationGrid";
import AudioPlayer from "../../../components/AudioPlayer/AudioPlayer";
import ConfirmModal from "../../../components/Modal/ConfirmModal";

class LiveStockForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      updateFlag: false,
      itemToDelete: "",
      audioToDelete: {},
      liveStock: {
        Id: "",
        StockName: "",
        Active: true,
        CreatedOn: "",
        CreatedBy: "",
        UpdatedBy: "",
        UpdatedOn: "",
        StockNameRequired: false,
        renderURL: ""
      },
      audioGridOpen : false,
      activeAudioAllocation: [],
      imageGridOpen: false,
      imageAllocation :[],
      modalStatus: false
    };
  }

  componentDidMount() {
    if (this.props.match.params.id !== undefined) {
      if (this.props.activeLiveStock.length !== 0) {
        let id = this.props.match.params.id;
        this.setCurrentLivestockToState(id);
      }
      // setTimeout(() => {
      //   this.setState({
      //     loading: false,
      //     updateFlag: true
      //   });
      // },2000);
    } else {
      setTimeout(() => {
        this.setState({
          loading: false
        });
      }, 1000);
    }
    if (this.props.liveStockError) {
      Toaster.Toaster("Something went wrong !", this.props.liveStockError);
    }
  }
  setCurrentLivestockToState(id) {
    this.props.getLiveStockAudioAllocations(id);
    let liveStock = _.find(this.props.activeLiveStock, function(liveStock) {
      return liveStock.Id === parseInt(id);
    });
    if (liveStock !== undefined) {
      if (liveStock.ImageURL) {
        liveStock.renderURL = `${AppConfig.serverURL}/${liveStock.ImageURL}`;
      }
      let imageAllocation = _.filter(this.props.imageFiles, {
        FilePath: liveStock.renderURL
      });
      setTimeout(() => {
          let activeAudioAllocation = _.filter(
            this.props.currentLivestockAudios,
            { Active: true }
          );
        this.setState({
          updateFlag: true,
          liveStock: liveStock,
          loading: false,
          activeAudioAllocation: activeAudioAllocation,
          audioGridOpen: true,
          imageGridOpen: true,
          imageAllocation: imageAllocation,
        });
      }, 3000);
    } else {
      this.props.history.push("/liveStock/liveStocks");
    }
  }
  onChangeName(event) {
    let liveStock = { ...this.state.liveStock };
    liveStock[event.target.name] = event.target.value;
    liveStock[event.target.name + "Required"] = false;
    this.setState({ liveStock });
  }
  onSubmit() {
    let liveStock = { ...this.state.liveStock };
    if (liveStock.StockName && liveStock.StockName.trim().length > 0) {
      if (this.state.updateFlag) {
        let liveStockUpdateData = _.pick(liveStock, [
          "Id",
          "StockName",
          "ImageURL",
          "UpdatedBy",
          "UpdatedOn",
          "Active"
        ]);
        liveStockUpdateData.UpdatedBy = localStorage.getItem("user");
        liveStockUpdateData.UpdatedOn = new Date();
        liveStockUpdateData.Active = true;
        this.props.updateLiveStock(liveStockUpdateData.Id, liveStockUpdateData);
      } else {
        let liveStockData = _.pick(liveStock, [
          "StockName",
          "ImageURL",
          "CreatedBy",
          "CreatedOn",
          "Active"
        ]);
        liveStockData.CreatedBy = localStorage.getItem("user");
        liveStockData.CreatedOn = new Date();
        liveStockData.Active = true;
        this.props.createliveStock(liveStockData);
      }
      this.setState({ loading: true });
      let message = "";
      let compRef = this;
      setTimeout(() => {
        compRef.props.liveStockError
          ? (message = compRef.props.liveStockError)
          : compRef.state.updateFlag
            ? (message = "Livestock updated successfully")
            : (message = "Livestock created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.props.liveStockError);
        setTimeout(() => {
          if (!compRef.props.liveStockError) {
            compRef.onReset();
            compRef.props.history.push("/liveStock/liveStocks");
          }
        }, 1000);
      }, 1000);
    } else {
      liveStock.StockNameRequired = true;
      this.setState({ liveStock: liveStock });
    }
  }
  onReset() {
    this.setState({
      liveStock: {
        Id: "",
        StockName: "",
        Active: true,
        CreatedOn: "",
        CreatedBy: "",
        StockNameRequired: false,
        renderURL: ""
      }
    });
  }
  /**--------------AUDIO ALLOCATION FUNCTIONS----------------- */
  onModalToggle(itemToDelete) {
    this.setState({
      itemToDelete: itemToDelete,
      modalStatus: !this.state.modalStatus
    });
  }
  audioToggleCollapse() {
    this.setState({
      audioGridOpen: !this.state.audioGridOpen
    });
  }
  onAddAudio(){
    if (this.props.match.params.id !== undefined) {
      this.props.history.push(
        `/liveStock/audioAllocation/${"liveStocks"}/${this.props.match.params
          .id}`
      );
    }
  }
  playAudio(cell, row) {
    return <AudioPlayer autoPlay={false} source={row.FilePath} />;
  }
  onDeleteAudio(cell, row) {
    return (
      <Link to={this} onClick={() => this.deleteAudio(row)}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }
  deleteAudio(row) {
    this.setState({ audioToDelete: row });
    this.onModalToggle("audio");
  }
  onConfirmDeleteAudio() {
    let audioToDelete = { ...this.state.audioToDelete };
    let compRef = this;
    audioToDelete.Active = false;
    audioToDelete.ActiveBy = localStorage.getItem("user");
    audioToDelete.ActiveOn = new Date();
    this.props.deactivateLiveStockAudioAllocations(audioToDelete.Id, audioToDelete);
    let displayMessage = "Livestock audio removed successfully";
    setTimeout(() => {
      let message = "";
      compRef.props.liveStockError
        ? (message = "Something went wrong !")
        : (message = displayMessage);
      Toaster.Toaster(message, compRef.props.liveStockError);
      // setTimeout(() => {
      //   if (!compRef.props.liveStockError) {
      //     //compRef.onNotificationToggle("audio");
      //   }
      // }, 1000);
      compRef.setCurrentLivestockToState(this.props.match.params.id);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
   /**------------------------Image Allocation functions ---------------------*/
  imageToggleCollapse() {
    this.setState({
      imageGridOpen: !this.state.imageGridOpen
    });
  }
  onAddImage() {
    if (this.props.match.params.id !== undefined) {
      this.props.history.push(
        `/liveStock/imageAllocation/${"liveStocks"}/${this.props.match.params
          .id}`
      );
    }
  }
  showImage(cell, row) {
    if (row.FilePath) {
      return (
        <img src={row.FilePath} style={{ height: 50, width: 50 }} alt="" />
      );
    } else {
      return;
    }
  }
  onDeleteliveStockImage(cell, row) {
    return (
      <Link to={this} onClick={() => this.onModalToggle("image")}>
        <i className="fa fa-trash" title="Delete" />
      </Link>
    );
  }
  onConfirmDeleteImage() {
    let liveStock = { ...this.state.liveStock };
    //this.setState({ notificationItem: "image" });
    let compRef = this;
    liveStock.UpdatedBy = localStorage.getItem("user");
    liveStock.UpdatedOn = new Date();
    liveStock.ImageURL = "";
    let image = _.pick(liveStock, [
      "Id",
      "ImageURL",
      "UpdatedBy",
      "UpdatedOn"
    ]);
    this.props.updateLivestockImage(image);
    let displayMessage = "Livestock image removed successfully";
    setTimeout(() => {
      let message = "";
      compRef.props.liveStockError
        ? (message = "Something went wrong !")
        : (message = displayMessage);
      Toaster.Toaster(message, compRef.props.liveStockError);
      // setTimeout(() => {
      //   if (!compRef.props.liveStockError) {
      //     //compRef.onNotificationToggle("image");
      //   }
      // }, 1000);
      compRef.setCurrentLivestockToState(this.props.match.params.id);
    }, 1000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    const liveStock = { ...this.state.liveStock };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock Form"
        navigation={true}
        navigationRoute="/liveStock/liveStocks"
      >
        <div className="div-padding">
          <FormGroup row>
            <Col xs="12" md="7">
              <FormGroup row>
                <Col xs="10" md="8">
                  <InputElement
                    type="text"
                    name="StockName"
                    label="Livestock name"
                    maxLength={250}
                    placeholder="Livestock name"
                    value={liveStock.StockName}
                    required={liveStock.StockNameRequired}
                    onChange={event => this.onChangeName(event)}
                  />
                </Col>
              </FormGroup>
            </Col>
            <Col md="5">
              {liveStock.renderURL ? (
                <img
                  src={liveStock.renderURL}
                  height={300}
                  width={350}
                  alt=""
                />
              ) : null}
            </Col>
          </FormGroup>
          {this.state.updateFlag ? (
              <div>
                <div style={{ marginTop: 0 }}>
                  <CollapseCards
                    subName="Audio Allocation"
                    buttonName="Add Audio"
                    buttonLink={this}
                    buttonClick={this.onAddAudio.bind(this)}
                    isOpen={this.state.audioGridOpen}
                    toggleCollapse={this.audioToggleCollapse.bind(this)}
                  >
                    <FormGroup row>
                      <Col xs="12">
                        <AudioAllocationGrid
                          audioAllocation={this.state.activeAudioAllocation}
                          playAudio={this.playAudio.bind(this)}
                          onDelete={this.onDeleteAudio.bind(this)}
                        />
                      </Col>
                    </FormGroup>
                  </CollapseCards>
                </div>
                <div style={{ marginTop: -30 }}>
                  <CollapseCards
                    subName="Image Allocation"
                    buttonName={
                      this.state.imageAllocation.length === 0
                        ? "Add Image"
                        : null
                    }
                    buttonLink={this}
                    buttonClick={this.onAddImage.bind(this)}
                    isOpen={this.state.imageGridOpen}
                    toggleCollapse={this.imageToggleCollapse.bind(this)}
                  >
                    <FormGroup row>
                      <Col xs="12">
                        <ImageAllocationGrid
                          imageAllocation={this.state.imageAllocation}
                          showImage={this.showImage.bind(this)}
                          onDelete={this.onDeleteliveStockImage.bind(this)}
                        />
                      </Col>
                    </FormGroup>
                  </CollapseCards>
                </div>
              </div>
            ) : null}

          {this.state.updateFlag ? (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Save
                </Button>
              </Col>
            </FormGroup>
          ) : (
            <FormGroup row>
              <Col md="1">
                <Button
                  className="theme-positive-btn"
                  onClick={() => this.onSubmit()}
                >
                  Create
                </Button>
              </Col>
              <Col md="1">
                <Button
                  className="theme-reset-btn"
                  onClick={() => this.onReset()}
                >
                  Reset
                </Button>
              </Col>
            </FormGroup>
          )}
        </div>
        {this.state.itemToDelete === "image" ? 
          <ConfirmModal
            isOpen={this.state.modalStatus}
            onModalToggle={this.onModalToggle.bind(this)}
            onConfirmDelete={this.onConfirmDeleteImage.bind(this)}
            title="Delete"
            message="Are you sure you want to remove this image ?"
          />
        : 
          <ConfirmModal
            isOpen={this.state.modalStatus}
            onModalToggle={this.onModalToggle.bind(this)}
            onConfirmDelete={this.onConfirmDeleteAudio.bind(this)}
            title="Delete"
            message="Are you sure you want to remove this audio ?"
          />
        }
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStock: state.liveStockReducer.activeLiveStock,
    inActiveLiveStock: state.liveStockReducer.inActiveLiveStock,
    liveStockError: state.liveStockReducer.liveStockError,
    imageFiles: state.mediaReducer.imageFiles,
    currentLivestockAudios: state.liveStockReducer.currentLivestockAudios
  };
};

const mapDispatchToProps = dispatch => {
  return {
      createliveStock : (liveStock) => dispatch(actions.createLiveStock(liveStock)),
      updateLiveStock : (id, liveStock) => dispatch(actions.updateLiveStock(id, liveStock)),
      getLiveStockAudioAllocations : (id) => dispatch(actions.getLiveStockAudioAllocations(id)),
      deactivateLiveStockAudioAllocations : (id,audioAllocation) => dispatch(actions.deactivateLiveStockAudioAllocations(id,audioAllocation)),
      updateLivestockImage: (image) => dispatch(actions.updateLivestockImage(image))
    };
};
export default connect(mapStateToProps, mapDispatchToProps)(LiveStockForm);



