import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import Loader from "../../components/Loader/Loader";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
import { FormGroup, Col, Button, Label } from "reactstrap";
import CardLayout from "../../components/Cards/CardLayout";
import DropdownSelect from "../../components/InputElement/Dropdown";
import AudioPlayer from "../../components/AudioPlayer/AudioPlayer";
import * as options from "../../constants/StatusConstants";
import _ from "lodash";
import { Async } from "react-select";
import "react-select/dist/react-select.css";
import NotificationModal from "./NotificationModal";
class AudioAllocationForm extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      renderURL: "",
      audioValue: "",
      fieldOptions: [],
      audioAllocation: {
        Id: "",
        LiveStockId: "",
        LiveStockBreedId: "",
        LiveStockBreedCategoryId: "",
        LiveStockStepId: "",
        LiveStockStepMaterialId: "",
        LangId: "",
        LangIdRequired: false,
        FieldType: "",
        FieldTypeRequired: false,
        AudioId: "",
        AudioIdRequired: false,
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true
      },
      audioCategory: "",
      id: "",
      error: "",
      notificationFlag: false
    };
  }
  componentDidMount() {
    let audioCategory = this.props.match.params.audioCategory;
    let id = parseInt(this.props.match.params.id);
    setTimeout(() => {
      let fieldOptions = [];
      let audioAllocation = { ...this.state.audioAllocation };
      if (audioCategory === "liveStocks") {
        audioAllocation.LiveStockId = id;
        fieldOptions = options.liveStockOptions;
      } else if (audioCategory === "liveStockBreed") {
        audioAllocation.LiveStockBreedId = id;
        fieldOptions = options.liveStockOptions;
      } else if (audioCategory === "liveStockBreedCategory") {
        audioAllocation.LiveStockBreedCategoryId = id;
        fieldOptions = options.liveStockOptions;
      } else if (audioCategory === "liveStockStep") {
        audioAllocation.LiveStockStepId = id;
        fieldOptions = options.liveStockStepOptions;
      } else if (audioCategory === "liveStockMaterial") {
        audioAllocation.LiveStockStepMaterialId = id;
        fieldOptions = options.liveStockMaterialOptions;
      } else {
        this.goBack();
      }
      this.setState({
        audioAllocation,
        loading: false,
        fieldOptions: fieldOptions,
        audioCategory,
        id
      });
    }, 1000);
  }
  handleAudioChange = audioSelected => {
    let audioAllocation = { ...this.state.audioAllocation };
    if (audioSelected !== null) {
      audioAllocation.AudioId = audioSelected.value;
      audioAllocation.AudioIdRequired = false;
      let audioValue = audioSelected;
      let audio = _.find(this.props.audioFiles, { Id: audioSelected.value });
      let renderURL = audio.FilePath;
      this.setState({ audioAllocation, audioValue: audioSelected, renderURL });
    } else {
      audioAllocation.AudioId = "";
      audioAllocation.AudioIdRequired = true;
      this.setState({ audioAllocation, audioValue: "", renderURL: "" });
    }
  };
  onFieldChange(value) {
    let audioAllocation = { ...this.state.audioAllocation };
    audioAllocation.FieldType = value;
    audioAllocation.FieldTypeRequired = false;
    this.setState({ audioAllocation });
  }
  onLanguageChange(value) {
    let audioAllocation = { ...this.state.audioAllocation };
    audioAllocation.LangId = value;
    audioAllocation.LangIdRequired = false;
    this.setState({ audioAllocation });
  }
  goBack() {
    this.props.history.goBack();
  }
  onSubmit() {
    let audioAllocation = { ...this.state.audioAllocation };
    let audioCategory = this.state.audioCategory;
    if (this.validAudioAllocation(audioAllocation)) {
      audioAllocation.CreatedBy = localStorage.getItem("user");
      audioAllocation.CreatedOn = new Date();
      if (audioCategory === "liveStocks") {
        this.props.postLiveStockAudioAllocations(audioAllocation);
      } else if (audioCategory === "liveStockBreed") {
        this.props.postBreedAudioAllocations(audioAllocation);
      } else if (audioCategory === "liveStockBreedCategory") {
        this.props.postBreedCategoryAudioAllocations(audioAllocation);
      } else if (audioCategory === "liveStockStep") {
        this.props.postLivestockStepAudioAllocations(audioAllocation);
      } else if (audioCategory === "liveStockMaterial") {
        this.props.postLivestockMaterialAudioAllocations(audioAllocation);
      } else {
        this.goBack();
      }
      let message = "";
      let compRef = this;
      this.setState({ loading: true });
      setTimeout(() => {
        if (audioCategory === "liveStocks") {
          if (compRef.props.liveStockError) {
            compRef.setState({ error: compRef.props.liveStockError });
          } else {
            compRef.setState({ error: "" });
          }
        } else if (audioCategory === "liveStockBreed") {
          if (compRef.props.liveStockBreedError) {
            compRef.setState({ error: compRef.props.liveStockBreedError });
          } else {
            compRef.setState({ error: "" });
          }
        } else if (audioCategory === "liveStockBreedCategory") {
          if (compRef.props.breedCategoryError) {
            compRef.setState({ error: compRef.props.breedCategoryError });
          } else {
            compRef.setState({ error: "" });
          }
        } else if (audioCategory === "liveStockStep") {
          if (compRef.props.liveStockStepError) {
            compRef.setState({ error: compRef.props.liveStockStepError });
          } else {
            compRef.setState({ error: "" });
          }
        } else if (audioCategory === "liveStockMaterial") {
          if (compRef.props.liveStockMaterialError) {
            compRef.setState({ error: compRef.props.liveStockMaterialError });
          } else {
            compRef.setState({ error: "" });
          }
        } else {
          compRef.setState({ error: "" });
        }
        compRef.state.error
          ? (message = compRef.state.error)
          : (message = "Audio allocation created successfully");
        compRef.setState({ loading: false });
        Toaster.Toaster(message, compRef.state.error);
        //compRef.onReset();
        setTimeout(() => {
          if (!compRef.state.error) {
            //compRef.goBack();
            compRef.onNotificationToggle();
          } else {
            compRef.goBack();
          }
        }, 1000);
      }, 1000);
    }
  }
  validAudioAllocation(allocation) {
    if (
      allocation.FieldType &&
      allocation.LangId &&
      allocation.AudioId &&
      (allocation.LiveStockId ||
        allocation.LiveStockBreedId ||
        allocation.LiveStockBreedCategoryId ||
        allocation.LiveStockStepId ||
        allocation.LiveStockStepMaterialId)
    ) {
      return true;
    } else {
      if (!allocation.FieldType) allocation.FieldTypeRequired = true;
      if (!allocation.LangId) allocation.LangIdRequired = true;
      if (!allocation.AudioId) allocation.AudioIdRequired = true;
      this.setState({ audioAllocation: allocation });
      return false;
    }
  }
  onReset() {
    let fieldOptions = [];
    if(this.props.match.params.audioCategory === "liveStockMaterial"){
      fieldOptions = options.liveStockMaterialOptions;
    }else if(this.props.match.params.audioCategory === "liveStockStep"){
      fieldOptions = options.liveStockStepOptions;
    }else{
      fieldOptions = options.liveStockOptions;      
    }
    this.setState({
      renderURL: "",
      audioValue: "",
      fieldOptions: fieldOptions,
      audioAllocation: {
        // Id: "",
        LiveStockId: this.props.match.params.id,
        LiveStockBreedId: this.props.match.params.id,
        LiveStockBreedCategoryId: this.props.match.params.id,
        LiveStockStepId: this.props.match.params.id,
        LiveStockStepMaterialId: this.props.match.params.id,
        LangId: "",
        LangIdRequired: false,
        FieldType: "",
        FieldTypeRequired: false,
        AudioId: "",
        AudioIdRequired: false,
        CreatedBy: "",
        CreatedOn: "",
        UpdatedBy: "",
        UpdatedOn: "",
        Active: true
      }
    });
  }
  onNotificationToggle(){
    this.setState({ notificationFlag : !this.state.notificationFlag});
  }
  render() {
    const audioAllocation = { ...this.state.audioAllocation };
    const options = this.props.audioOptions;
    const getOptions = (input, callback) => {
      let filterOpt = [];
      if (input) {
        filterOpt = _.filter(options, function(audio) {
          if (
            _.includes(audio.label.toLowerCase(), input.toLowerCase()) ||
            audio.label.toLowerCase() == input.toLowerCase()
          ) {
            return audio;
          }
        });
      }
      setTimeout(() => {
        callback(null, {
          options: filterOpt,
          complete: true
        });
      }, 500);
    };
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Audio Allocation Form"
        navigation={true}
        navigationRoute={this}
        onClick={this.goBack.bind(this)}
      >
        <div className="div-padding">
          <FormGroup row />
          <FormGroup row>
            <Col xs="12" md="5">
              <Label>Field Type</Label>
              <DropdownSelect
                name="Feild type"
                placeholder="Select field type"
                options={this.state.fieldOptions}
                value={audioAllocation.FieldType}
                required={audioAllocation.FieldTypeRequired}
                onChange={this.onFieldChange.bind(this)}
              />
            </Col>
            <Col md="5">
              <Label>Language</Label>
              <DropdownSelect
                name="Language"
                placeholder="Select language"
                options={this.props.languagesList}
                value={audioAllocation.LangId}
                required={audioAllocation.LangIdRequired}
                onChange={this.onLanguageChange.bind(this)}
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Col xs="12" md="5">
              <Label>Audio</Label>
              <Async
                name="form-field-name"
                cacheOptions
                value={this.state.audioValue}
                defaultOptions
                onChange={this.handleAudioChange.bind(this)}
                loadOptions={getOptions}
              />
              {audioAllocation.AudioIdRequired ? (
                <div className="help-block">*Audio is required</div>
              ) : null}
            </Col>
            {this.state.renderURL ? (
              <Col md="5">
                <Label />
                <AudioPlayer source={this.state.renderURL} autoPlay={false} />
              </Col>
            ) : null}
          </FormGroup>

          <FormGroup row>
            <Col md="1">
              <Button
                className="theme-positive-btn"
                onClick={() => this.onSubmit()}
              >
                Create
              </Button>
            </Col>
            <Col md="1">
              <Button
                className="theme-reset-btn"
                onClick={() => this.onReset()}
              >
                Reset
              </Button>
            </Col>
          </FormGroup>
        </div>
        <ToastContainer autoClose={1000} />
        <NotificationModal
          active={true}
          category="audio"
          notificationContext={this.props.match.params.audioCategory}
          contextId={parseInt(this.props.match.params.id)}
          openFlag={this.state.notificationFlag}
          toggleFunction={this.onNotificationToggle.bind(this)}
          shouldGoBack={true}
          fieldType={this.state.audioAllocation.FieldType}
          {...this.props}
        />
      </CardLayout>
    );
  }
}
export const mapStateToProps = state => {
  return {
    audioOptions: state.mediaReducer.audioOptions,
    audioFiles: state.mediaReducer.audioFiles,
    languagesList: state.languagesReducer.languagesList,
    liveStockError: state.liveStockReducer.liveStockError,
    liveStockBreedError: state.liveStockReducer.liveStockBreedError,
    breedCategoryError: state.liveStockReducer.breedCategoryError,
    liveStockStepError: state.liveStockReducer.liveStockStepError,
    liveStockMaterialError: state.liveStockReducer.liveStockMaterialError
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    postLiveStockAudioAllocations: audioAllocation =>
      dispatch(actions.postLiveStockAudioAllocations(audioAllocation)),
    postBreedAudioAllocations: audioAllocation =>
      dispatch(actions.postBreedAudioAllocations(audioAllocation)),
    postBreedCategoryAudioAllocations: audioAllocation =>
      dispatch(actions.postBreedCategoryAudioAllocations(audioAllocation)),
    postLivestockStepAudioAllocations: audioAllocation =>
      dispatch(actions.postLivestockStepAudioAllocations(audioAllocation)),
    postLivestockMaterialAudioAllocations: audioAllocation =>
      dispatch(actions.postLivestockMaterialAudioAllocations(audioAllocation))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(
  AudioAllocationForm
);



