import React, { Component } from "react";
import { Route } from "react-router-dom";
import LiveStocks from "./LiveStocks.js/LivestockList";
import LiveStockForm from "./LiveStocks.js/LiveStockForm";
import LiveStockSteps from "./LiveStockSteps/LiveStockSteps";
import LiveStockStepForm from "./LiveStockSteps/LiveStockStepForm";
import LiveStockMaterial from "./LiveStockMaterials/LiveStockMaterial";
import LiveStockMaterialForm from "./LiveStockMaterials/LiveStockMaterialForm";
import LiveStockBreed from "./LiveStockBreeds/LiveStockBreed";
import LiveStockBreedForm from "./LiveStockBreeds/LiveStockBreedForm";
import LiveStockBreedCatergory from "./LiveStockBreedCategory/LiveStockBreedCatergory";
import LiveStockBreedCatergoryForm from "./LiveStockBreedCategory/LiveStockBreedCatergoryForm";
import AudioAllocationForm from "./AudioAllocationForm";
import ImageAllocationForm from "./ImageAllocationForm";
export default class CropsCultivation extends Component {
  render() {
    return (
      <div>
        <Route
          exact
          path={`${this.props.match.path}/liveStocks`}
          component={LiveStocks}
        />
        <Route
          path={`${this.props.match.path}/liveStocks/liveStockForm/:id?`}
          component={LiveStockForm}
        />

        <Route
          exact
          path={`${this.props.match.path}/liveStockSteps`}
          component={LiveStockSteps}
        />
        <Route
          path={`${this.props.match
            .path}/liveStockSteps/liveStockStepForm/:id?`}
          component={LiveStockStepForm}
        />
        <Route
          exact
          path={`${this.props.match.path}/liveStockMaterials`}
          component={LiveStockMaterial}
        />
        <Route
          path={`${this.props.match
            .path}/liveStockMaterials/liveStockMaterialForm/:id?`}
          component={LiveStockMaterialForm}
        />
        <Route
          exact
          path={`${this.props.match.path}/liveStockBreeds`}
          component={LiveStockBreed}
        />
        <Route
          path={`${this.props.match
            .path}/liveStockBreeds/liveStockBreedForm/:id?`}
          component={LiveStockBreedForm}
        />
        <Route
          exact
          path={`${this.props.match.path}/liveStockBreedCategory`}
          component={LiveStockBreedCatergory}
        />
        <Route
          path={`${this.props.match
            .path}/liveStockBreedCategory/liveStockBreedCategoryForm/:id?`}
          component={LiveStockBreedCatergoryForm}
        />
          <Route
          path={`${this.props.match.path}/audioAllocation/:audioCategory?/:id?/:audioId?`}
          component={AudioAllocationForm}
        />
        <Route
          path={`${this.props.match.path}/imageAllocation/:imageCategory?/:id?`}
          component={ImageAllocationForm}
        />
      </div>
    );
  }
}



