import React, { Component } from "react";
import {
  Modal,
  ModalHeader,
  ModalBody,
  Button,
  ModalFooter,
  FormGroup,
  Col,
  Label
} from "reactstrap";
import { connect } from "react-redux";
import * as actions from "../../store/actions";
import DropdownSelect from "../../components/InputElement/Dropdown";
import _ from "lodash";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../constants/Toaster";
class NotificationModal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      notificationData: {
        category: this.props.category,
        notificationContext: this.props.notificationContext,
        contextId: this.props.contextId,
        fieldType: this.props.fieldType,
        stateId: "",
        districtId: "",
        grampanchayatId: "",
        villageIdList: [],
        languageId: "",
        districtDisabled: true,
        grampanchayatDisabled: true,
        villageDisabled: true,
        languageIdRequired: false,
        Active: this.props.active
      },
      shouldGoBack: this.props.shouldGoBack,
      districtOptions: [],
      grampanchayatOptions: [],
      villageOptions: []
    };
  }
  handleNotificationData(event) {
    let notificationData = { ...this.state.notificationData };
    notificationData[event.target.name] = event.target.value;
    notificationData[event.target.name + "Required"] = false;
    this.setState({ notificationData });
  }
  handleStateValueChange(value) {
    let notificationData = { ...this.state.notificationData };
    let districtOptions = [...this.state.districtOptions];
    if (value !== null) {
      notificationData.stateId = value;
      notificationData.districtDisabled = false;
      notificationData.districtId = "";
      notificationData.grampanchayatId = "";
      notificationData.villageIdList = "";
      districtOptions = _.filter(this.props.districtsList, { stateId: value });
    } else {
      notificationData.stateId = "";
      notificationData.districtDisabled = true;
      notificationData.grampanchayatDisabled = true;
      notificationData.villageDisabled = true;
      notificationData.districtId = "";
      notificationData.grampanchayatId = "";
      notificationData.villageIdList = "";
      districtOptions = [];
    }
    this.setState({
      notificationData,
      districtOptions,
      grampanchayatOptions: [],
      villageOptions: []
    });
  }
  handleDistrictValueChange(value) {
    let notificationData = { ...this.state.notificationData };
    let grampanchayatOptions = [...this.state.grampanchayatOptions];
    if (value !== null) {
      notificationData.districtId = value;
      notificationData.grampanchayatId = "";
      notificationData.villageIdList = "";
      notificationData.grampanchayatDisabled = false;
      grampanchayatOptions = _.filter(this.props.grampanchayatsList, {
        districtId: value
      });
    } else {
      notificationData.districtId = "";
      notificationData.grampanchayatDisabled = true;
      notificationData.villageDisabled = true;
      notificationData.grampanchayatId = "";
      notificationData.villageIdList = "";
      grampanchayatOptions = [];
    }
    this.setState({
      notificationData,
      grampanchayatOptions,
      villageOptions: []
    });
  }
  handleGrampanchayatValueChange(value) {
    let notificationData = { ...this.state.notificationData };
    let villageOptions = [...this.state.villageOptions];
    if (value !== null) {
      notificationData.grampanchayatId = value;
      notificationData.villageIdList = "";
      notificationData.villageDisabled = false;
      villageOptions = _.filter(this.props.villagesList, {
        grampanchayatId: value
      });
    } else {
      notificationData.grampanchayatId = "";
      notificationData.villageDisabled = true;
      notificationData.villageIdList = "";
      villageOptions = [];
    }
    this.setState({ notificationData, villageOptions });
  }
  handleVillageValueChange(value) {
    let notificationData = { ...this.state.notificationData };
    notificationData.villageIdList = value;
    this.setState({ notificationData });
  }

  handleLanguageChange(value) {
    let notificationData = { ...this.state.notificationData };
    notificationData.languageId = value;
    notificationData.languageIdRequired = false;
    this.setState({ notificationData });
  }

  onSubmit() {
    if (this.validInput()) {
      let notificationData = { ...this.state.notificationData };
      notificationData.category = this.props.category;
      notificationData.fieldType = this.props.fieldType
        ? this.props.fieldType
        : "";
      let villageList = [];
      if (notificationData.villageIdList.length > 0) {
        notificationData.villageIdList.forEach(village => {
          villageList.push(village.value);
        });
        notificationData.villageIdList = villageList;
      } else {
        notificationData.villageIdList = villageList;
      }
      let notification = _.pick(notificationData, [
        "fieldType",
        "category",
        "notificationContext",
        "contextId",
        "stateId",
        "districtId",
        "grampanchayatId",
        "villageIdList",
        "languageId",
        "Active"
      ]);
      this.onSendNotification(notification);
    }
  }
  onSendNotification(notification) {
    this.props.sendLivestockNotification(notification);
    let compRef = this;
    setTimeout(() => {
      let message = "Notification sent successfully";
      compRef.props.livestockNotificationError
        ? (message = compRef.props.livestockNotificationError)
        : message;
      compRef.onReset();
      Toaster.Toaster(message, compRef.props.livestockNotificationError);
      setTimeout(() => {
        if (!compRef.props.livestockNotificationError) {
          compRef.props.toggleFunction();
          if (compRef.state.shouldGoBack) {
            compRef.props.history.goBack();
          }
        } else {
          if (compRef.state.shouldGoBack) {
            compRef.props.history.goBack();
          }
        }
      }, 1000);
    }, 2000);
  }
  validInput() {
    let notificationData = { ...this.state.notificationData };
    if (notificationData.languageId) {
      return true;
    } else {
      if (!notificationData.languageId)
        notificationData.languageIdRequired = true;
      this.setState({ notificationData: notificationData });
      return false;
    }
  }
  onReset() {
    this.setState({
      notificationData: {
        category: this.props.category,
        notificationContext: this.props.notificationContext,
        contextId: this.props.contextId,
        stateId: "",
        districtId: "",
        grampanchayatId: "",
        villageIdList: [],
        languageId: "",
        districtDisabled: true,
        grampanchayatDisabled: true,
        villageDisabled: true,
        languageIdRequired: false,
        Active: this.props.active
      },
      shouldGoBack: this.props.shouldGoBack,
      districtOptions: [],
      grampanchayatOptions: [],
      villageOptions: []
    });
  }
  render() {
    const notificationData = { ...this.state.notificationData };
    return (
      <Modal isOpen={this.props.openFlag} className={"modal-lg "}>
        <ModalHeader>Send Notification</ModalHeader>
        <ModalBody>
          <FormGroup row>
            <Col xs="12" md="4">
              <Label>State</Label>
              <DropdownSelect
                name="State"
                placeholder="Select state"
                options={this.props.statesList}
                value={notificationData.stateId}
                onChange={this.handleStateValueChange.bind(this)}
              />
            </Col>
            <Col md="4">
              <Label>District</Label>
              <DropdownSelect
                name="District"
                placeholder="Select district"
                disabled={notificationData.districtDisabled}
                options={this.state.districtOptions}
                value={notificationData.districtId}
                onChange={this.handleDistrictValueChange.bind(this)}
              />
            </Col>
            <Col md="4">
              <Label>Grampanchayat</Label>
              <DropdownSelect
                name="Grampanchayat"
                placeholder="Select grampanchayat"
                disabled={notificationData.grampanchayatDisabled}
                options={this.state.grampanchayatOptions}
                value={notificationData.grampanchayatId}
                onChange={this.handleGrampanchayatValueChange.bind(this)}
              />
            </Col>
          </FormGroup>
          <FormGroup row>
            <Col xs="12" md="4">
              <Label>Village</Label>
              <DropdownSelect
                name="Village"
                placeholder="Select villages"
                disabled={notificationData.villageDisabled}
                options={this.state.villageOptions}
                multiple={true}
                value={notificationData.villageIdList}
                onChange={this.handleVillageValueChange.bind(this)}
              />
            </Col>
            <Col md="4">
              <Label>Language</Label>
              <DropdownSelect
                name="Language"
                placeholder="Select language"
                options={this.props.languagesList}
                value={notificationData.languageId}
                onChange={this.handleLanguageChange.bind(this)}
                required={notificationData.languageIdRequired}
              />
            </Col>
          </FormGroup>
        </ModalBody>
        <ModalFooter>
          <Button color="success" onClick={this.onSubmit.bind(this)}>
            Send Notification
          </Button>
          {/* {this.props.cropNotificationError ? (
            <Button color="danger" onClick={this.props.toggleFunction}>
              Cancel
            </Button>
          ) : null} */}
          <Button color="danger" onClick={this.props.toggleFunction}>
              Cancel
            </Button>
        </ModalFooter>
      </Modal>
    );
  }
}

export const mapStateToProps = state => {
  return {
    statesList: state.stateReducer.statesList,
    districtsList: state.districtReducer.districtsList,
    grampanchayatsList: state.grampanchayatReducer.grampanchayatsList,
    villagesList: state.villageReducer.villagesList,
    languagesList: state.languagesReducer.languagesList,
    livestockNotificationError:
      state.notificationReducer.livestockNotificationError
  };
};

export const mapDispatchToProps = dispatch => {
  return {
    sendLivestockNotification: notification =>
      dispatch(actions.sendLivestockNotification(notification))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(NotificationModal);



