import React, { Component } from "react";
import { connect } from "react-redux";
import * as actions from "../../../store/actions";
import CardLayout from "../../../components/Cards/CardLayout";
import { FormGroup, Col, Row } from "reactstrap";
import Loader from "../../../components/Loader/Loader";
import ConfirmModal from "../../../components/Modal/ConfirmModal";
import * as constants from "../../../constants/StatusConstants";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import * as Toaster from "../../../constants/Toaster";
import DropdownSelect from "../../../components/InputElement/Dropdown";
import { Link } from "react-router-dom";
import ActiveLiveStockTable from "./ActiveLiveStockTable";
import InActiveLiveStockTable from "./InActiveLiveStockTable";
import AppConfig from "../../../constants/AppConfig";

class Livestock extends Component {
  constructor(props) {
    super(props);
    this.state = {
      loading: true,
      tableStatus: true,
      modalStatus: false,
      liveStockToDelete: {},
      messageModelStatus: false,
      message: ""
    };
  }

  componentDidMount() {
    setTimeout(() => {
      this.setState({
        loading: false
      });
    }, 1000);
  }
  onTablestatusChange(value) {
    if (value != null) {
      this.setState({
        tableStatus: value
      });
    }
  }
  onDeleteLiveStock(cell, row) {
    if (this.state.tableStatus) {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-trash" title="Deactivate" />
        </Link>
      );
    } else {
      return (
        <Link to={this} onClick={() => this.onModalToggle(row)}>
          <i className="fa fa-check-square-o" title="Activate" />
        </Link>
      );
    }
  }
  onEditLiveStock(cell, row) {
    return (
      <Link to={`${this.props.match.url}/liveStockForm/${row.Id}`}>
        <i className="fa fa-pencil" title="Edit" />
      </Link>
    );
  }
  showImage(cell, row) {
    if(row.ImageURL){
      return (
        <img
          src={`${AppConfig.serverURL}/${row.ImageURL}`}
          style={{ height: 50, width: 50 }}
          alt=""
        />
      );
    }else{
      return;
    }
  }

  onModalToggle(liveStock) {
    this.setState({
      liveStockToDelete: liveStock,
      modalStatus: !this.state.modalStatus
    });
  }
  onConfirmDelete() {
    let liveStockToDelete = { ...this.state.liveStockToDelete };
    let message = "";
    liveStockToDelete.ActiveBy = localStorage.getItem("user");
    if (this.state.tableStatus) {
      liveStockToDelete.Active = false;
      message = "Livestock deactivated successfully";
    } else {
      liveStockToDelete.Active = true;
      message = "Livestock activated successfully";
    }
    this.props.deactivateLiveStock(liveStockToDelete.Id, liveStockToDelete);
    this.setState({ loading: true });
    let compRef = this;
    setTimeout(() => {
      compRef.props.liveStockError
        ? (message = compRef.props.liveStockError)
        : message;
      compRef.setState({ loading: false });
      Toaster.Toaster(message, compRef.props.liveStockError);
    }, 2000);
    this.setState({
      modalStatus: !this.state.modalStatus
    });
  }
  render() {
    return this.state.loading ? (
      <Loader loading={this.state.loading} />
    ) : (
      <CardLayout
        name="Livestock"
        buttonName="Add Livestock"
        buttonLink={`${this.props.match.url}/liveStockForm`}
      >
        <Row className="address-drop-margin">
          <Col xs="12" md="10" />
          <Col md="2">
            <DropdownSelect
              label="Status"
              options={constants.tableStatus}
              value={this.state.tableStatus}
              onChange={this.onTablestatusChange.bind(this)}
              search={false}
              clear={false}
              simpleValue
            />
          </Col>
        </Row>
        <FormGroup row>
          <Col xs="12">
            {this.state.tableStatus ? (
              <ActiveLiveStockTable
                liveStocks={this.props.activeLiveStock}
                onDelete={this.onDeleteLiveStock.bind(this)}
                onEdit={this.onEditLiveStock.bind(this)}
                showImage={this.showImage.bind(this)}
              />
            ) : (
              <InActiveLiveStockTable
                liveStocks={this.props.inActiveLiveStock}
                onDelete={this.onDeleteLiveStock.bind(this)}
                showImage={this.showImage.bind(this)}
              />
            )}
          </Col>
        </FormGroup>
        <ConfirmModal
          isOpen={this.state.modalStatus}
          onModalToggle={this.onModalToggle.bind(this)}
          onConfirmDelete={this.onConfirmDelete.bind(this)}
          title={this.state.tableStatus ? "Deactivate" : "Activate"}
          message={
            this.state.tableStatus
              ? "Are you sure you want to deactivate this livestock record ?"
              : "Are you sure you want to activate this livestock record ?"
          }
        />
        <ToastContainer autoClose={1000} />
      </CardLayout>
    );
  }
}

const mapStateToProps = state => {
  return {
    activeLiveStock: state.liveStockReducer.activeLiveStock,
    inActiveLiveStock: state.liveStockReducer.inActiveLiveStock,
    liveStockError: state.liveStockReducer.liveStockError
  };
};

const mapDispatchToProps = dispatch => {
  return {
    getLiveStock: () => dispatch(actions.getLiveStocks()),
    deactivateLiveStock: (id, liveStock) =>
      dispatch(actions.deactivateLiveStock(id, liveStock))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(Livestock);



